.container{
    min-height: 90vh;
    padding: 1vh 5vh;
}

.headerTable{
    width: 70%;
    height: 10vh;
    font-size: 18px;
    display: grid;
    grid-template-columns: 1fr 3fr 3fr 2fr;
    grid-template-rows: auto;    
    align-items: center;    
}

.headerTable span{
    border: 1px solid #fff;
    padding: 3vmin;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.addButtonContainer{
    margin: 2vmin 0;
    width: 100%;
    display: flex;
    justify-content: center;
}

.addButton{
    height: 7vmin;
    width: 7vmin;
    color: #fff;
    font-size: 2.5vmin;
    background: #282c34;
    border: 1px solid lightgray;
    border-radius: 100px;
    transition: all ease-in-out 0.3s;
    user-select: none;
}

.addButton:hover, .calculateButton:hover{
    background: #6C6F78;
    cursor: pointer;
}


.outputTable{
    width: 70%;
    height: 10vh;
    font-size: 18px;
    display: grid;
    grid-template-columns: 5fr 2fr;
    grid-template-rows: auto;    
    align-items: center;    
}

.outputTable span{
    border: 1px solid #fff;
    padding: 3vmin;
    height: 100%;
    display: flex;
    align-items: center;
}

.outputTable span:nth-child(1){
    justify-content: flex-end
}

.outputTable span:nth-child(2){
    display: flex;
    justify-content: space-around
}

.calculateButton{
    height: 4vmin;
    width: 7vmin;
    color: #fff;
    font-size: 13px;
    background: #282c34;
    border: 1px solid lightgray;
    border-radius: 100px;
    transition: all ease-in-out 0.3s;
    user-select: none;
}