body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header{
    height: 10vh;
    padding: 3vh;
    text-align: left;
}
.container{
    min-height: 90vh;
    padding: 1vh 5vh;
}

.headerTable{
    width: 70%;
    height: 10vh;
    font-size: 18px;
    display: grid;
    grid-template-columns: 1fr 3fr 3fr 2fr;
    grid-template-rows: auto;    
    align-items: center;    
}

.headerTable span{
    border: 1px solid #fff;
    padding: 3vmin;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.addButtonContainer{
    margin: 2vmin 0;
    width: 100%;
    display: flex;
    justify-content: center;
}

.addButton{
    height: 7vmin;
    width: 7vmin;
    color: #fff;
    font-size: 2.5vmin;
    background: #282c34;
    border: 1px solid lightgray;
    border-radius: 100px;
    transition: all ease-in-out 0.3s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.addButton:hover, .calculateButton:hover{
    background: #6C6F78;
    cursor: pointer;
}


.outputTable{
    width: 70%;
    height: 10vh;
    font-size: 18px;
    display: grid;
    grid-template-columns: 5fr 2fr;
    grid-template-rows: auto;    
    align-items: center;    
}

.outputTable span{
    border: 1px solid #fff;
    padding: 3vmin;
    height: 100%;
    display: flex;
    align-items: center;
}

.outputTable span:nth-child(1){
    justify-content: flex-end
}

.outputTable span:nth-child(2){
    display: flex;
    justify-content: space-around
}

.calculateButton{
    height: 4vmin;
    width: 7vmin;
    color: #fff;
    font-size: 13px;
    background: #282c34;
    border: 1px solid lightgray;
    border-radius: 100px;
    transition: all ease-in-out 0.3s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.HourInputContainer{
    width: 70%;
    height: 10vh;
    font-size: 18px;
    display: grid;
    grid-template-columns: 1fr 3fr 3fr 2fr;
    grid-template-rows: auto;    
    align-items: center;
}

.gridElement{
    border: 1px solid #fff;
    padding: 3vmin;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.InputsContainer input{
    width: 30%;
    height: 3vmin;
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif
}

.InputsContainer span{
    padding: 0 1.5vmin;
}
