.HourInputContainer{
    width: 70%;
    height: 10vh;
    font-size: 18px;
    display: grid;
    grid-template-columns: 1fr 3fr 3fr 2fr;
    grid-template-rows: auto;    
    align-items: center;
}

.gridElement{
    border: 1px solid #fff;
    padding: 3vmin;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.InputsContainer input{
    width: 30%;
    height: 3vmin;
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif
}

.InputsContainer span{
    padding: 0 1.5vmin;
}